import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Snackbar from "@material-ui/core/Snackbar";
import SnackbarContent from "@material-ui/core/SnackbarContent";

// Components
import { Success, ErrorIcon } from "@cvccorp-components/icons";

//  Styles
import { styles } from "./styles";

const CustomSnackbar = ({ hasIcon, hasBtn, type, isOpen, close, labelClose, classes, message }) => (
    <Snackbar open={isOpen} onClose={close}>
        <SnackbarContent
            classes={{ message: classes.message }}
            className={classes[type]}
            message={
                <div className={classes.content}>
                    <span className={classes.text}>
                        {hasIcon && type === "success" && (
                            <span className={classes.spacing}>
                                <Success size="medium" />
                            </span>
                        )}
                        {hasIcon && type === "error" && <ErrorIcon size="medium" />}
                        {message}
                    </span>
                    {hasBtn && (
                        <button type="button" className={classes.closeButton} onClick={close}>
                            {labelClose}
                        </button>
                    )}
                </div>
            }
        />
    </Snackbar>
);

CustomSnackbar.propTypes = {
    classes: PropTypes.oneOfType([PropTypes.object, PropTypes.string]).isRequired,
    type: PropTypes.string,
    isOpen: PropTypes.bool,
    hasIcon: PropTypes.bool,
    message: PropTypes.string,
    hasBtn: PropTypes.bool,
    close: PropTypes.func,
    labelClose: PropTypes.string,
};

export default withStyles(styles, { withTheme: true })(CustomSnackbar);
